const stateList = (function () {
    console.log("stateList module invoked");
    // Instances stored in this list.
    const _list = {};
    return {
        register: (name, instance) => {
            _list[name] = instance;
        },
        unregister: (name) => {
            // これでinstanceもさくじょしていることになるかしら
            delete _list[name];
        },
        // nameで指定するんじゃなくて、
        // 型引数で指定できるようにしたいなぁ
        caller: (name) => {
            return _list[name];
        },
        showList: () => {
            console.log("stateList::_list:");
            console.log(_list);
        },
        length: () => {
            return Object.keys(_list).length;
        },
    };
})();
